@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

/* entry previews / list */
.project-list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 2.5rem;
}
.project-preview {
  padding: 10px 0;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
  flex: 1 0 25%;
}
.project-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}
.project-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}
.project-preview a{
  text-decoration: none;
}

/* entry details page */
.project-details h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 10px;
}
.project-details div {
  margin: 20px 0;
}
.project-details button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  min-width: 100px;
}
.project-details-buttons {
  display: flex;
  column-gap: 25px;
}

/* create new entry form */
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

/* editing new entry form */
.edit {
  margin: 0 auto;
  text-align: center;
}
.edit-list{
  display: flex;
  column-gap: 5px;
}
.edit label {
  text-align: left;
  display: block;
}
.edit h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.edit input, .edit textarea, .edit select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.edit button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}